.hero {
  height: calc(100vh - 55.99px);
  width: 100%;
  position: relative;
  background-color: rgba(0, 0, 0, 0.05);
  z-index: 2;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
.hero__image-overlay {
  position: absolute;
  left: 25;
  right: 25;
  top: 0;
  bottom: 0;
  z-index: -5;
  width: 50%;
  height: calc(100% - 50px);
}
.hero__container-items {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.btn.btn-primary {
  z-index: 4;
}
